#about {
  padding: 100px 0;
}
#about h3 {
  font-size: 22px;
  margin: 0 0 20px;
  color: #2f3c7e;
}
#about h2 {
  position: relative;
  margin-bottom: 15px;
  padding-bottom: 15px;
  color: #2f3c7e;
}
#about h2::after {
  position: absolute;
  content: "";
  background: linear-gradient(to right, #2f3c7e 0%, #5ca9fb 100%);
  height: 4px;
  width: 60px;
  bottom: 0;
  left: 0;
}
#about .about-text li {
  font-size: 18px;
  margin-bottom: 24px;
  margin-left: 6px;
  list-style: none;
  padding: 0;
}
#about .about-text li:before {
  content: "\f00c";
  font-family: "FontAwesome";
  color: #5ca9fb;
  font-size: 11px;
  font-weight: 300;
  padding-right: 8px;
}
#about img {
  width: 520px;
  margin-top: 37px;
  border-right: 0;
}
#about p {
  line-height: 24px;
  margin: 30px 0;
}
